.item{
    display: grid;
    justify-items: center;
    margin-bottom: 20px;
    text-decoration: none;
}
.item:hover .name{
    color: #fff;
}
.img{
    width: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    border-radius: 50%;
    margin-bottom: 5px;
}

.img img{
    max-width: 100%;
}

.name{
    text-align: center;
    font-size: 16px;
    color: rgb(200, 200, 200);
    transition: all ease .2s;
}
